/* eslint-disable */
import '../sass/chart.sass'
import * as d3 from "d3";

var pieData = [
        { name: '0-20', value: 32 },
        { name: '21-40', value: 45 },
        { name: '41-60', value: 17 },
        { name: '61 и более', value: 6 },
    ],
    lineData = [
        { name: 'Услышал по радио', value: 12 },
        { name: 'Видел объявление в газете', value: 5 },
        { name: 'Нашел в интернете', value: 50 },
        { name: 'Слышал от других людей', value: 14 },
    ]

writePie(pieData, '.chart')
writeGist(lineData, '.chart2')
writeVertGist(lineData, '.chart3')

// цвета
function getColors(i) {
    var colors = ['#E94335', '#4385F6', '#32A653', '#F8BC04']
    return colors[i]
}

// круговой график
function writePie(dt, elem) {
    var height = 220,
        width = 220,
        data = dt
    var arc = d3
        .arc()
        .outerRadius(width / 2)
        .innerRadius(0)
    var pie = d3
        .pie()
        .sort(null)
        .value(function (d) {
            return d.value
        })
    var svg = d3
        .select(elem)
        .append('svg')
        .attr('class', 'ch-chart_svgPie')
        .attr('width', width)
        .attr('height', height)
        .append('g')
        .attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')')
    var g = svg.selectAll('g').data(pie(data)).enter().append('g')

    g.append('path')
        .attr('d', arc)
        .style('fill', function (d, i) {
            return getColors(i)
        })
        .style('stroke', '#ffffff')
        .style('stroke-width', '2px')

    // legends
    var allValue = data.reduce((sum, current) => sum + current.value, 0)
    d3.select(elem)
        .selectAll('.ch-chart_legends')
        .data(data)
        .enter()
        .append('div')
        .html((d, i) => {
            var persent = Math.round((d.value / allValue) * 100)
            return (
                '<div class="ch-chart_legend"><span class="ch-chart_legendTag" style="background: ' +
                getColors(i) +
                '"></span>' +
                d.name +
                ' <span class="ch-chart_legendPerc">(' +
                persent +
                '%)</span></div>'
            )
        })
}

function writeGist(dt, elem) {
    var data = dt

    var svgWidth = 300,
        barHeight = 23,
        barMargin = 10

    var graphValue = data.map(i => i.value)
    var scaleFactor = svgWidth / d3.max(graphValue) / 1.2
    var svgHeight = (barHeight + barMargin) * data.length + 50

    var graph = d3
        .select(elem)
        .append('svg')
        .attr('class', 'ch-chart_svgLine')
        .attr('width', svgWidth)
        .attr('height', svgHeight)

    var bar = graph
        .selectAll('g')
        .data(graphValue)
        .enter()
        .append('g')
        .attr('transform', function (d, i) {
            return (
                'translate(20,' +
                (i * (barHeight + barMargin) + barMargin * 2) +
                ')'
            )
        })
    bar.append('rect')
        .attr('width', function (d) {
            return d * scaleFactor
        })
        .attr('height', barHeight)
        .style('fill', function (d, i) {
            return getColors(i)
        })

    bar.append('text')
        .attr('x', 10)
        .attr('y', barHeight / 2)
        .attr('dy', '.35em')
        .attr('font-size', '13px')
        .attr('font-weight', '600')
        .text(function (d) {
            return d
        })

    // ширина нижней оси
    var scaleWidth = d3.max(graphValue) * scaleFactor
    var xScale = d3
        .scaleLinear()
        .domain([0, d3.max(graphValue)])
        .range([0, scaleWidth])

    var x_axis = d3.axisBottom().scale(xScale)

    //отступ снизу
    var xAxisTranslate = svgHeight - 20
    graph
        .append('g')
        .attr('class', 'ch-chart_g')
        .attr('transform', 'translate(20, ' + xAxisTranslate + ')')
        .style('font-size', '12')
        .attr('font-family', false)
        .call(x_axis)
    graph.select('.ch-chart_g').selectAll('path').remove()
    graph
        .select('.ch-chart_g')
        .selectAll('line')
        .attr('stroke', 'rgba(196,196,196,1.0)')
        .attr('y1', 12)
        .attr('y2', 0)
        .attr('transform', 'translate(0, -6)')
    graph
        .select('.ch-chart_g')
        .selectAll('text')
        .attr('fill', 'rgba(196,196,196,1.0)')
    graph.select('.ch-chart_g').select('.tick').remove()

    // линия слева
    graph
        .append('line')
        .attr('x1', 20)
        .attr('x2', 20)
        .attr('y1', 0)
        .attr('y2', svgHeight)
        .style('stroke', 'rgba(196,196,196,1.0)')
        .style('stroke-width', 1)
    // линия снизу
    graph
        .append('line')
        .attr('x1', 0)
        .attr('x2', svgWidth)
        .attr('y1', xAxisTranslate)
        .attr('y2', xAxisTranslate)
        .style('stroke', 'rgba(196,196,196,1.0)')
        .style('stroke-width', 1)

    // legends
    d3.select(elem)
        .selectAll('.ch-chart_legends')
        .data(data)
        .enter()
        .append('div')
        .html(
            (d, i) =>
                '<div class="ch-chart_legend"><span class="ch-chart_legendTag" style="background: ' +
                getColors(i) +
                '"></span>' +
                d.name +
                ' <span class="ch-chart_legendPerc">(' +
                d.value +
                ')</span></div>'
        )
}

function writeVertGist(dt, elem) {
    var data = dt

    var svgWidth = 300,
        barHeight = 23,
        barMargin = 10

    var graphValue = data.map(i => i.value)
    var svgHeight = (barHeight + barMargin) * data.length + 50
    var scaleFactor = svgHeight / d3.max(graphValue) / 1.2

    var graph = d3
        .select(elem)
        .append('svg')
        .attr('class', 'ch-chart_svgLine')
        .attr('width', svgWidth)
        .attr('height', svgHeight)

    var bar = graph
        .selectAll('g')
        .data(graphValue)
        .enter()
        .append('g')
        .attr('transform', function (d, i) {
            return (
                'translate(' +
                (i * (barHeight + barMargin) + barMargin * 2 + 20) +
                ',' +
                (svgHeight - d * scaleFactor - 20) +
                ')'
            )
        })
    bar.append('rect')
        .attr('height', function (d) {
            return d * scaleFactor
        })
        .attr('width', barHeight)
        .style('fill', function (d, i) {
            return getColors(i)
        })

    bar.append('text')
        .attr('x', 10)
        .attr('y', barHeight / 2)
        .attr('dy', '.35em')
        .attr('font-size', '13px')
        .attr('font-weight', '600')
        .text(function (d) {
            return d
        })

    // ширина нижней оси
    var scaleWidth = d3.max(graphValue) * scaleFactor
    var xScale = d3
        .scaleLinear()
        .domain([0, d3.max(graphValue)])
        .range([svgHeight, 0])

    var x_axis = d3.axisLeft().scale(xScale)

    //отступ снизу
    var xAxisTranslate = svgHeight - 20
    graph
        .append('g')
        .attr('class', 'ch-chart_g')
        .attr('transform', 'translate(23, -20)')
        .style('font-size', '12')
        .attr('font-family', false)
        .call(x_axis)
    graph.select('.ch-chart_g').selectAll('path').remove()
    graph
        .select('.ch-chart_g')
        .selectAll('line')
        .attr('stroke', 'rgba(196,196,196,1.0)')
        .attr('y1', 12)
        .attr('y2', 0)
        .attr('transform', 'translate(0, -6)')
    graph
        .select('.ch-chart_g')
        .selectAll('text')
        .attr('fill', 'rgba(196,196,196,1.0)')
    graph.select('.ch-chart_g').select('.tick').remove()

    // линия слева
    graph
        .append('line')
        .attr('x1', 20)
        .attr('x2', 20)
        .attr('y1', 0)
        .attr('y2', svgHeight)
        .style('stroke', 'rgba(196,196,196,1.0)')
        .style('stroke-width', 1)
    // линия снизу
    graph
        .append('line')
        .attr('x1', 0)
        .attr('x2', svgWidth)
        .attr('y1', xAxisTranslate)
        .attr('y2', xAxisTranslate)
        .style('stroke', 'rgba(196,196,196,1.0)')
        .style('stroke-width', 1)

    // legends
    d3.select(elem)
        .selectAll('.ch-chart_legends')
        .data(data)
        .enter()
        .append('div')
        .html(
            (d, i) =>
                '<div class="ch-chart_legend"><span class="ch-chart_legendTag" style="background: ' +
                getColors(i) +
                '"></span>' +
                d.name +
                ' <span class="ch-chart_legendPerc">(' +
                d.value +
                ')</span></div>'
        )
}
